@import 'vars';

.donate-container {
  padding: 24px 40px;
  background: $support-section-color;
  box-shadow: 0px 10px 10px rgba(245, 245, 247, 0.1);
  border-radius: 16px;
}

.gradient {
  background: linear-gradient(149deg,
      rgba(79, 46, 232, 1) rgba(245, 245, 247, 0.1));
  background-size: 400%;
}

.donate-paragraph {
  color: $header-color-ligth;
  font-family: $main-font;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  letter-spacing: -0.04em;
}

.donate-header {
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin-bottom: 20px;
}

.donate-index {
  font-family: $main-font;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: $header-color-ligth;
  letter-spacing: -0.04em;
}

.donate-funds-list {
  width: 159px;
}

.donate-funds-visible {
  width: 170px;
  height: 208px;
  overflow: hidden;

  margin-bottom: 10px;
}

.donate-fund {
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 16px;
}

.donate-item-link {
  margin-left: 16px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.donate-img {
  -webkit-filter: brightness(0) invert(1);
  filter: brightness(0) invert(1);
  height: 32px;
  object-fit: contain;
}

.donate-emblem {
  width: 20px;
  height: 32px;
}

.donate-emblem {
  width: 20px;
  height: 32px;
}

.donate-img {
  transition: transform 500ms ease-out;
}

.donate-img:hover {
  transform: scale(1.15);
}

.donate-img:active,
.donate-img:focus {
  transform: scale(0.85);
}

/*-------------------------- Scroll bar--------------------------------------- */
.donate-funds-visible::-webkit-scrollbar {
  width: 0%;
  background-color: transparent;
}

.donate-funds-visible::-webkit-scrollbar-thumb {
  background-color: transparent;
}

/*-------------------------- !Scroll bar!--------------------------------------- */

/*---------------------------- Arrow -------------------------------------------*/
.donate-button {
  width: 32px;
  height: 32px;
  background-color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  padding: 0;
  margin: 0 auto;
}

.donate-block-arrow {
  display: flex;
  align-items: center;
  justify-content: center;
}

.donate-arrow {
  fill: $accent-color-ligth;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  transform: scale(0.7);
  transition: all 2s cubic-bezier(0.4, 0, 0.2, 1);
}

.donate-button:hover,
.donate-arrow:focus {
  background-color: $bgc-ligth-theme;
}

/* -----------------------! Arrow!--------------------------------- */

/*---------------------------- Arrow up -------------------------------------------*/
.donate-button-up {
  width: 32px;
  height: 32px;
  background-color: $accent-color-ligth;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  padding: 0;
  margin: 0 auto;
  display: block;
  display: none;
}

.donate-arrow-up {
  fill: $secondary-text-color-dark;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotate(180deg) scale(0.7);
  transition: all 2s cubic-bezier(0.4, 0, 0.2, 1);
  width: 32px;
  height: 32px;
}

.donate-button-up:focus,
.donate-arrow-up:hover {
  fill: $primary-text-color-dark;
  border-radius: 50%;
}

/* -----------------------! Arrow up!--------------------------------- */

@media screen and (min-width: 768px) {

  .donate-header {
    margin-bottom: 25px;
  }

  .donate-funds-visible {
    height: 310px;
  }
}