@import 'vars';

.switch {
  font-size: 17px;
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.span-theme {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #239af5;
  transition: 0.4s;
  border-radius: 30px;
}

.span-theme:before {
  position: absolute;
  content: '';
  height: 18px;
  width: 18px;
  border-radius: 20px;
  left: 0px;
  bottom: 0px;
  z-index: 2;
  background-color: #ffffff;
  transition: 0.6s;
}

.sun svg {
  position: absolute;
  top: -2px;
  left: 20px;
  z-index: 1;
  width: 18px;
  height: 24px;
}

.moon svg {
  fill: #ffffff;
  position: absolute;
  top: 1px;
  left: 0px;
  z-index: 1;
  width: 18px;
  height: 18px;
}

/* .switch:hover */
.sun svg {
  animation: rotate 15s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* .switch:hover */
.moon svg {
  animation: tilt 1.5s linear infinite;
}

@keyframes tilt {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(10deg);
  }

  75% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-10deg);
  }
}

.theme-toggle-js:checked + .span-theme {
  background-color: #183153;
}

.theme-toggle-js:focus + .span-theme {
  box-shadow: 0 0 1px #183153;
}

.theme-toggle-js:checked + .span-theme:before {
  transform: translateX(0px);
}

