/*------------------MODAL-----------------------------*/

/* .header-mobile-box {
  position: relative;
} */

.header-modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 66px;
  left: 0;

  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  background: radial-gradient(
    183.6% 183.01% at 59.76% -23.42%,
    #4f2ee8 18.03%,
    #ffffff 100%
  );

  transform: translateX(100%);
  /* opacity: 0; */
  visibility: hidden;
  pointer-events: none;
  transition: transform 500ms linear, visibility 500ms linear;
}

.header-modal.menu-is-open {
  transform: translateX(0%);
  /* opacity: 1; */
  visibility: visible;
  pointer-events: auto;
}

.opacity {
  animation-duration: 0.9s;
  animation-timing-function: ease-out;
}

.header-modal-image {
  background-image: url(../images/Group1k.png);
  width: 100%;
  height: 350px;
  position: absolute;
  z-index: 2;
  top: 53%;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
}

@media screen and (min-device-pixel-ratio: 2) and (max-width: 767px),
