/*---------------------------------------------*/
@media screen and (max-width: 767px) {
  .header-nav {
    display: none;
  }

  /* .header-modal {
    display: none;
  } */

  .header-user {
    display: none;
  }

  .header-container {
    padding: 18px 22.5px;
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
}

.header-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.switch {
  position: absolute;
  right: 45px;
}

@media screen and (min-width: 768px) {
  .header-mobile-burger {
    display: none;
  }

  .header-container {
    background-color: rgba(255, 255, 255, 0.9);
    pointer-events: all;
    padding-top: 14px;
    padding-bottom: 14px;
    padding-left: 22.5px;
    padding-right: 22.5px;
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    max-width: 768px;
  }

  .header-modal-container,
  .header-modal,
  .header-modal-content {
    display: none;
  }
}

@media screen and (min-width: 1440px) {
  .header-link-item-home {
    margin-left: 76px;
  }

  .header-link-item-shopping {
    margin-left: 24px;
  }

  .header-modal-container,
  .header-modal,
  .header-modal-content {
    display: none;
  }

  .header-container {
    max-width: 1440px;
  }
}

