.switch {
  width: 40px;
  height: 20px;
  font-size: 17px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.span-theme {
  cursor: pointer;
  background-color: #239af5;
  border-radius: 30px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.span-theme:before {
  content: "";
  height: 18px;
  width: 18px;
  z-index: 2;
  background-color: #fff;
  border-radius: 20px;
  transition: all .6s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.sun svg {
  z-index: 1;
  width: 18px;
  height: 24px;
  position: absolute;
  top: -2px;
  left: 20px;
}

.moon svg {
  fill: #fff;
  z-index: 1;
  width: 18px;
  height: 18px;
  position: absolute;
  top: 1px;
  left: 0;
}

.sun svg {
  animation: rotate 15s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.moon svg {
  animation: tilt 1.5s linear infinite;
}

@keyframes tilt {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(10deg);
  }

  75% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-10deg);
  }
}

.theme-toggle-js:checked + .span-theme {
  background-color: #183153;
}

.theme-toggle-js:focus + .span-theme {
  box-shadow: 0 0 1px #183153;
}

.theme-toggle-js:checked + .span-theme:before {
  transform: translateX(0);
}

